import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "app/store";

export interface loginStatusState {
	state?: true | false;
}

const initialState: loginStatusState = {};

export const loginStatusSlice = createSlice({
	name: "loginStatus",
	initialState,
	reducers: {
		changeLoginStatus: (state, action: PayloadAction<loginStatusState>) => {
			return {
				...state,
				...action.payload,
			};
		},
	},
});

// Action creators are generated for each case reducer function
export const { changeLoginStatus } = loginStatusSlice.actions;

export const selectCurrentLoginStatus = (state: RootState) => state.loginStatus;

export default loginStatusSlice.reducer;
