import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import Avatar from "shared/Avatar/Avatar";
import Badge from "shared/Badge/Badge";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import ButtonSecondary from "shared/Button/ButtonSecondary";
import Comment from "shared/Comment/Comment";
import NcImage from "shared/NcImage/NcImage";
import SocialsList from "shared/SocialsList/SocialsList";
import Textarea from "shared/Textarea/Textarea";
import { Helmet } from "react-helmet";
import { _getImgRd, _getPersonNameRd, _getTitleRd } from "contains/fakeData";
import queryString from "query-string";
import axios from "axios";

const BlogSingle = () => {
	const [title, setTitle] = React.useState("");
	const [description, setDescription] = React.useState("");
	const [content, setContent] = React.useState("");
	const [userEmail, setUserEmail] = React.useState("");

	useEffect(() => {
		init();
	}, []);

	var init = () => {
		const id = getQueryId();
		if (id) {
			axios
				.get(`${process.env.REACT_APP_BASE_URL}/api/blog/info?id=${id}`)
				.then((res: any) => {
					console.log(res.data.data);
					setTitle(res.data.data.title);
					setDescription(res.data.data.description);
					setContent(res.data.data.content);
					setUserEmail(res.data.data.userEmail);
				});
		}
	};

	const getQueryId = () => {
		const query = queryString.parse(window.location.search);
		if (query.id) {
			console.log(query.id);
			return query.id;
		} else {
			return null;
		}
	};

	const renderHeader = () => {
		return (
			<header className="container rounded-xl">
				<div className="max-w-screen-md mx-auto space-y-5">
					<h1
						className=" text-neutral-900 font-semibold text-3xl md:text-4xl md:!leading-[120%] lg:text-4xl dark:text-neutral-100 max-w-4xl "
						title="Quiet ingenuity: 120,000 lunches and counting">
						{title}
					</h1>
					<span className="block text-base text-neutral-500 md:text-lg dark:text-neutral-400 pb-1">
						{description}
					</span>

					<div className="w-full border-b border-neutral-100 dark:border-neutral-800"></div>
				</div>
			</header>
		);
	};

	const renderContent = () => {
		return (
			<div
				id="single-entry-content"
				className="prose prose-sm !max-w-screen-md sm:prose lg:prose-lg mx-auto dark:prose-invert">
				<div dangerouslySetInnerHTML={{ __html: content }}></div>
			</div>
		);
	};

	const renderTags = () => {
		return (
			<div className="max-w-screen-md mx-auto flex flex-wrap">
				<a
					className="nc-Tag inline-block bg-white text-sm text-neutral-600 py-2 rounded-lg border border-neutral-100  md:px-4 dark:bg-neutral-700 dark:border-neutral-700 hover:border-neutral-200 dark:hover:border-neutral-6000 mr-2 mb-2"
					href="##">
					Garden
				</a>
				<a
					className="nc-Tag inline-block bg-white text-sm text-neutral-600 py-2 rounded-lg border border-neutral-100  md:px-4 dark:bg-neutral-700 dark:border-neutral-700 hover:border-neutral-200 dark:hover:border-neutral-6000 mr-2 mb-2"
					href="##">
					Jewelry
				</a>
				<a
					className="nc-Tag inline-block bg-white text-sm text-neutral-600 py-2 rounded-lg border border-neutral-100  md:px-4 dark:bg-neutral-700 dark:border-neutral-700 hover:border-neutral-200 dark:hover:border-neutral-6000 mr-2 mb-2"
					href="##">
					Tools
				</a>
			</div>
		);
	};

	const renderAuthor = () => {
		return (
			<div className="max-w-screen-md mx-auto ">
				<div className="nc-SingleAuthor flex">
					{/* <Avatar sizeClass="w-11 h-11 md:w-24 md:h-24" /> */}
					<div className="flex flex-col ml-3 max-w-lg sm:ml-5 space-y-1">
						<span className="text-xs text-neutral-400 uppercase tracking-wider">
							WRITEN BY
						</span>
						<h2 className="text-lg font-semibold text-neutral-900 dark:text-neutral-200">
							<a href="/ncmaz/author/the-demo-author-slug">{userEmail}</a>
						</h2>
					</div>
				</div>
			</div>
		);
	};

	const renderCommentForm = () => {
		return (
			<div className="max-w-screen-md mx-auto pt-5">
				<h3 className="text-xl font-semibold text-neutral-800 dark:text-neutral-200">
					Responses (14)
				</h3>
				<form className="nc-SingleCommentForm mt-5">
					<Textarea />
					<div className="mt-2 space-x-3">
						<ButtonPrimary>Submit</ButtonPrimary>
						<ButtonSecondary>Cancel</ButtonSecondary>
					</div>
				</form>
			</div>
		);
	};

	const renderCommentLists = () => {
		return (
			<div className="max-w-screen-md mx-auto">
				<ul className="nc-SingleCommentLists space-y-5">
					<li>
						<Comment />
						<ul className="pl-4 mt-5 space-y-5 md:pl-11">
							<li>
								<Comment isSmall />
							</li>
						</ul>
					</li>
					<li>
						<Comment />
						<ul className="pl-4 mt-5 space-y-5 md:pl-11">
							<li>
								<Comment isSmall />
							</li>
						</ul>
					</li>
				</ul>
			</div>
		);
	};

	const renderPostRelated = (_: any, index: number) => {
		return (
			<div
				key={index}
				className="relative aspect-w-3 aspect-h-4 rounded-3xl overflow-hidden group">
				<Link to={"/blog-single"} />
				<NcImage
					className="object-cover w-full h-full transform group-hover:scale-105 transition-transform duration-300"
					src={_getImgRd()}
				/>
				<div>
					<div className="absolute bottom-0 inset-x-0 h-1/2 bg-gradient-to-t from-black"></div>
				</div>
				<div className="flex flex-col justify-end items-start text-xs text-neutral-300 space-y-2.5 p-4">
					<Badge name="Categories" />
					<h2 className="block text-lg font-semibold text-white ">
						<span className="line-clamp-2">{_getTitleRd()}</span>
					</h2>

					<div className="flex">
						<span className="block text-neutral-200 hover:text-white font-medium truncate">
							{_getPersonNameRd()}
						</span>
						<span className="mx-1.5 font-medium"></span>
						<span className="font-normal truncate">May 20, 2021</span>
					</div>
				</div>
				<Link to={"/blog-single"} />
			</div>
		);
	};

	return (
		<div className="nc-PageSingle pt-8 lg:pt-16 ">
			<Helmet>
				<title>Single Blog || Ciscryp React Template</title>
			</Helmet>
			{renderHeader()}
			<div className="nc-SingleContent container space-y-10">
				{renderContent()}
				<div className="max-w-screen-md mx-auto border-b border-t border-neutral-100 dark:border-neutral-700"></div>
				{renderAuthor()}
				{renderCommentForm()}
				{renderCommentLists()}
			</div>
			<div className="relative bg-neutral-100 dark:bg-neutral-800 py-16 lg:py-28 mt-16 lg:mt-24">
				<div className="container ">
					<h2 className="text-3xl font-semibold">Related posts</h2>
					<div className="mt-10 grid sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6 md:gap-8">
						{/*  */}
						{[1, 1, 1, 1].filter((_, i) => i < 4).map(renderPostRelated)}
						{/*  */}
					</div>
				</div>
			</div>
		</div>
	);
};

export default BlogSingle;
