import React, { useEffect, useState } from "react";
import queryString from "query-string";

const Test = () => {
  const [nfts, setNfts] = useState([]);

  useEffect(() => {
    getNFTData();

    // setMetaCheck(true);
  }, []);

  //url walletId 
  const getQueryId = () => {
    const query = queryString.parse(window.location.search);
    if (query.id) {
      console.log(query.id)
      return query.id;
    } else {
      return null;
    }
  };

  const getNFTData = async () => {
    // if (!walletAddress) return;
    //url walletID  nft  return
	  if (!getQueryId()) {
      return;
    }

    const response = await fetch(
      `https://api.rarible.org/v0.1/items/byOwner/?owner=ETHEREUM:0x4765273c477c2dc484da4f1984639e943adccfeb`
    );

    const data = await response.json();
    console.log(data.items)
    setNfts(data.items);
  };

  return (
    <div>
      {nfts && nfts.length ? 
      nfts.map((nft:any) => {
        return (
          <>
      <div>
      <div >
          <p>name : {nft?.meta.name}</p>
          <b>creators : {nft.creators[0].account}</b>
          <p>id : {nft.id}</p>
          <b>blockchain : {nft.blockchin}</b>
          <p>contract : {nft.contract}</p>
          <p>lastUpdatedAt : {nft.lastUpdatedAt}</p>
          <div>setUrl(url = {nft.lastUpdatedAt});</div>
          {/* <div>setUrl(url = {nft.meta.content[0].url});</div>            .......!!!!!  */}
          <p>mintedAt : {nft.meta.mintedAt}</p>
          <p>supply : {nft.supply}</p>
          <p>totalStock : {nft.totalStock}</p>
        </div>
      </div>
      </>
        )
      })
      :
      null}
    </div>
  );
};

export default Test;
