import React from "react";
import { useHistory } from "react-router-dom";
import { Helmet } from "react-helmet";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import ButtonSecondary from "shared/Button/ButtonSecondary";
import ReactQuill from "react-quill";
import axios from "axios";
import Cookies from "universal-cookie";
import "react-quill/dist/quill.snow.css";

const PostCreate = () => {
	const [title, setTitle] = React.useState("");
	const [description, setDescription] = React.useState("");
	const [content, setContent] = React.useState("");
	var history = useHistory();

	var handleSubmit = async () => {
		if (!title == null || title === "") {
			alert("Title is required");
			return;
		}
		if (!content == null || content === "") {
			alert("Content is required");
			return;
		}

		const cookies = new Cookies();
		const token = cookies.get("token");

		const data = {
			title,
			description,
			content,
		};

		try {
			const response = await axios.post(
				`${process.env.REACT_APP_BASE_URL}/api/blog/create`,
				data,
				{ headers: { Authorization: `Bearer ${token}` } }
			);
			if (response.status === 200) {
				const id = response.data.data;
				history.push("/blog-single?id=" + id);
			}
		} catch (e: any) {
			if (e.response.status === 500) {
				alert("Server error");
			}
			console.dir(e);
			alert("Unknown error");
		}
	};

	var modules = {
		toolbar: [
			//[{ 'font': [] }],
			[{ header: [1, 2, false] }],
			["bold", "italic", "underline", "strike", "blockquote"],
			[
				{ list: "ordered" },
				{ list: "bullet" },
				{ indent: "-1" },
				{ indent: "+1" },
			],
			["link"],
			[{ align: [] }, { color: [] }, { background: [] }], // dropdown with defaults from theme
			["clean"],
		],
	};

	var formats = [
		//'font',
		"header",
		"bold",
		"italic",
		"underline",
		"strike",
		"blockquote",
		"list",
		"bullet",
		"indent",
		"link",
		"image",
		"align",
		"color",
		"background",
	];

	const onChange = (value: any) => {
		setContent(value);
	};

	const renderHeader = () => {
		return (
			<header className="container rounded-xl">
				<div className="max-w-screen-md mx-auto space-y-5">
					<input
						className="block bg-inherit rounded w-full border-2 text-neutral-900 font-semibold text-3xl md:text-4xl md:!leading-[120%] lg:text-4xl dark:text-neutral-100 max-w-4xl"
						type="text"
						placeholder="Title"
						onChange={(e) => setTitle(e.target.value)}
					/>
					<textarea
						className="block resize-none bg-inherit rounded w-full border-2 text-neutral-500 md:text-lg dark:text-neutral-400 pb-1"
						placeholder="Description"
						onChange={(e) => setDescription(e.target.value)}
					/>

					<div className="w-full border-b border-neutral-100 dark:border-neutral-800"></div>
				</div>
			</header>
		);
	};

	const renderContent = () => {
		return (
			<div
				id="single-entry-content"
				className="prose prose-sm !max-w-screen-md sm:prose lg:prose-lg mx-auto dark:prose-invert">
				<ReactQuill
					style={{ height: "600px" }}
					theme="snow"
					modules={modules}
					formats={formats}
					value={content}
					onChange={(content, delta, source, editor) =>
						onChange(editor.getHTML())
					}
				/>
			</div>
		);
	};

	return (
		<div className="nc-PageSingle pt-8 lg:pt-16 ">
			<Helmet>
				<title>Single Blog || Ciscryp React Template</title>
			</Helmet>
			{renderHeader()}

			<div className="nc-SingleContent container space-y-10">
				{renderContent()}
				<div className="flex justify-between items-center max-w-screen-md py-5 mx-auto border-b border-t border-neutral-100 dark:border-neutral-700">
					<ButtonSecondary sizeClass="px-4 py-2 sm:px-5">
						Connect Wallet
					</ButtonSecondary>
					<ButtonPrimary sizeClass="px-4 py-2 sm:px-5" onClick={handleSubmit}>
						Submit
					</ButtonPrimary>
				</div>
			</div>
		</div>
	);
};

export default PostCreate;
